.tab-selector {
  display: flex;
  align-items: center;
  &-item-wrapper {
  }
  &-item {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 32px;
    border: 1px solid #000;
    margin-left: 18px;
    cursor: pointer;
  }
  .selected {
    background-color: gray;
    color: white;
  }
}
